body {
	font-family: $font-family;
	font-size: $font-size;
	line-height: $line-height;
	color: $text-color;
}

// .h1,
// .h2,
// .h3,
// .h4,
// .h5,
// .h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-family--headings;
	font-weight: 500;
	line-height: $line-height;
	color: currentColor;
}

// .h1,
// .h2,
// .h3,
h1,
h2,
h3 {
	margin-top: $line-height;
	margin-bottom: ($line-height / 2);
}

// .h4,
// .h5,
// .h6,
h4,
h5,
h6 {
	margin-top: ($line-height / 2);
	margin-bottom: ($line-height / 2);
}

// .h1,
h1 {
	font-size: $font-size--h1;
}

// .h2,
h2 {
	font-size: $font-size--h2;
}

// .h3,
h3 {
	font-size: $font-size--h3;
}

// .h4,
h4 {
	font-size: $font-size--h4;
}

// .h5,
h5 {
	font-size: $font-size--h5;
}

// .h6,
h6 {
	font-size: $font-size--h6;
}

// .p,
p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
	margin-top: ($line-height / 2);
	margin-bottom: ($line-height / 2);
}

address {
	font-style: normal;
}

ul,
ol {
	padding-left: 1.2em;
}

ol ol,
ul ul,
ol ul,
ul ol {
	margin-top: 0;
	margin-bottom: 0;
}

li {
	line-height: inherit;
}

dt {
	font-weight: 700;
}

dd {
	margin-left: 0;

	@media (min-width: $screen-sm) {
		margin-left: 1.8em;
	}

	& + dt {
		margin-top: ($line-height / 2);
	}
}

hr {
	margin-top: $line-height;
	margin-bottom: $line-height;
	border: 0;
	border-top: 1px solid $border-color;
}

// .small,
small {
	font-size: $font-size--small;
}

sub,
sup {
	font-size: 70%;
}

// .mark,
mark {
	background: #fcf8e3;
	padding: 0.1em 0.3em;
}

b,
strong {
	font-weight: bolder;
}

abbr[title] {
	border-bottom: 1px dotted;
}

blockquote {
	padding: 0;
	margin-left: 0;
	margin-right: 0;
}

code,
kbd,
pre,
samp {
	font-family: $font-family--monospace;
}

code {
	padding: 0.06em 0.3em;
	color: $text-color;
	background-color: $gray-lightest;
	border-radius: $border-radius;
}

kbd {
	padding: 0.06em 0.3em;
	color: $text-color;
	background-color: $gray-lightest;
	border-radius: $border-radius;

	kbd {
		padding: 0;
	}
}

pre {
	display: block;
	width: 100%;
	overflow-x: auto;
	tab-size: 2;
}

a {
	color: inherit;
	&:hover,
	&:focus {
		text-decoration: none;
		color: lighten(#000, 45%);
	}
}
